/* Base styles */
:root {
    --primary-color: #3b82f6;
    --primary-hover: #2563eb;
    --secondary-color: #f3f4f6;
    --danger-color: #ef4444;
    --success-color: #22c55e;
    --border-color: #e5e7eb;
}

.hotel-container {
    padding: 2rem;
    max-width: 72rem;
    margin: 0 auto;
}

.hotel-card {
    background-color: white;
    border-radius: 0.5rem;
    box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1);
    padding: 1.5rem;
    margin-bottom: 2rem;
}

.controls-container {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
}

.room-input {
    border: 1px solid var(--border-color);
    border-radius: 0.375rem;
    padding: 0.5rem 0.75rem;
    width: 8rem;
}

.btn {
    padding: 0.5rem 1rem;
    border-radius: 0.375rem;
    font-weight: 500;
    transition: all 0.2s;
}

.building {
    display: flex;
    justify-content: center;
    padding: 20px;
}

.lift {
    width: 60px;
    border: 1px solid black;
    margin: 10px;
    border-radius: 0.375rem;
}

.btn-primary {
    background-color: var(--primary-color);
    color: white;
}

.btn-primary:hover {
    background-color: var(--primary-hover);
}

.btn-secondary {
    border: 1px solid var(--border-color);
    background-color: white;
}

.btn-secondary:hover {
    background-color: var(--secondary-color);
}

.error-message {
    background-color: #fee2e2;
    border: 1px solid #fca5a5;
    color: #dc2626;
    padding: 0.75rem 1rem;
    border-radius: 0.375rem;
    margin-bottom: 1rem;
}

.success-message {
    background-color: #dbeafe;
    border: 1px solid #93c5fd;
    color: #2563eb;
    padding: 0.75rem 1rem;
    border-radius: 0.375rem;
    margin-bottom: 1rem;
}

.floors-container {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.floor-card {
    border-radius: 0.375rem;
    padding: 5px;
}

.rooms-grid {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    gap: 0.5rem;
}

.room-cell {
    padding: 20px;
    text-align: center;
    font-size: 5px;
    border-radius: 0.25rem;
    border: 1px solid var(--border-color);
    transition: all 0.2s;
}

.room-available {
    background-color: white;
    border-color: black;
}

.room-occupied {
    background-color: #fee2e2;
    border-color: #fca5a5;
}

.room-random {
    background-color: #808080;
    border-color: #666666;
}

.room-selected {
    outline: 2px solid var(--primary-color);
    outline-offset: -2px;
}

/* Responsive adjustments */
@media (max-width: 640px) {
    .hotel-container {
        padding: 1rem;
    }
    
    .controls-container {
        flex-direction: column;
    }
    
    .room-input {
        width: 100%;
    }
    
    .rooms-grid {
        grid-template-columns: repeat(5, 1fr);
    }
}